<template>
<section class="wrap has_header_wrap">
	<section class='wrap_in' :class="{ unscrollable: isUnscRollable }">
		<div class="product_in">
			<div class="search_top">
				<form @click="jumpSearch" class="d_flex" action="javascript:;">
					<input 
						type="text" 
						placeholder="请输入搜索内容"
						v-model="searchCon">
					<button>搜索</button>
				</form>
			</div>	
			<div class="screens">
				<div class="lists d_flex">
					<!-- <a 
						href="javascript:;" 
						v-for=" (sl,index) in screensLists"
						:class="[ index==(screensLists.length-1)?'screen_a':'' , {'cur': (index+1)===clickNum} ]"
						@click="clickScreens( sl,index )">
						{{ sl.name }}
						<i></i>
					</a> -->
					<a 
						href="javascript:;" 
						v-for=" (sl,index) in screensLists"
						:class="[ index==(screensLists.length-1)?'screen_a':'' , {'cur': clickNum[index+1].isCur} ]"
						@click="clickScreens( sl,index )">
						{{ sl.name }}
						<i></i>
					</a>
				</div>
				<transition name="fade">
					<!-- 全部分类 -->
					<div 
						class="all_solt d_flex"
						v-show="curScreen===1">
						<div class="left">
							<dl>
								<!-- <dt>全部分类</dt> -->
								<dd 
									:class="{ 'cur': curKs===0 }"
									@click="nextSort( 'allSort', 0 )">全部分类</dd>
								<dd
									:class="{ 'cur': index+1==curKs }"
									v-for=" (list,index) in ks "
									@click="nextSort( list, index )">{{list.productTypeName}}</dd>
							</dl>
						</div>
						<div class="right">
							<dl>
								<!-- <dt @click="searchSort('allSort',0)">全部{{ parentName }}</dt> -->
								<!-- <dd 
									:class="{cur : curRightSort===0}"
									@click="searchSort('allSort',0)">全部{{ parentName }}</dd> -->
								<!-- <dd class="cur">高血压</dd> -->
								<!-- <dd 
									:class="{cur : (index+1)===curRightSort}"
									v-for=" (cs,index) in childSort "
									@click="searchSort(cs,index)">{{ cs.productTypeName }}</dd> -->
								<!-- <dd 
									:class="{cur : (index+1)===curRightSort}"
									v-for=" (cs,index) in childSort "
									@click="searchSort(cs,index)">
									{{ index==0?'全部'+parentName: cs.productTypeName}}
									</dd> -->
								<dd 
									:class="{cur : curRightSort===0}"
									@click="searchSort('allSort',0)">全部{{ parentName }}</dd>
								<dd 
									:class="{cur : (index+1)===curRightSort}"
									v-for=" (cs,index) in childSort "
									@click="searchSort(cs,index)">
									{{ cs.productTypeName}}
									</dd>
							</dl>
						</div>
					</div>
					<!-- 全部分类 end -->
				</transition>
				<!-- 品牌筛选 -->
				<transition name="fade">
					<div class="brand_solt"
					v-show='curScreen===2'>
						<div class="top">
							<a 
								:class="{ cur: curBrandIndex === index }"
								v-for="(bl,index) in brandList" 
								@click="chooseBrand(bl,index)"
								href="javascript:;">{{ bl.brandName }}</a>
						</div>
						<div class="bottom d_flex">
							<button
								@click="resetBrand">重置</button>
							<button
								@click="confirmBrand">确定</button>
						</div>
					</div>
				</transition>
				<!-- 品牌筛选 end -->
				<!-- 价格筛选 -->
				<transition name="fade">
					<div 
						class="brand_solt price_solt"
						v-show='curScreen===3'>
						<!-- 价格区分 暂不要 -->
						<div v-if="false" class="top">
							<a class="cur" href="javascript:;">不限</a>
							<a href="javascript:;">100元以下</a>
							<a href="javascript:;">100-200元</a>
							<a href="javascript:;">200元以上</a>
							<a href="javascript:;">1000元以上</a>
						</div>
						<!-- 价格区分 暂不要 end -->
						<div class="center">
							<input 
								type="number" 
								placeholder="最低价"
								v-model.trim="minPrice">
							<span>元</span>
							<span>~</span>
							<input 
								type="number" 
								placeholder="最高价"
								v-model.trim="maxPrice">
							<span>元</span>
						</div>
						<div class="bottom d_flex">
							<button
								@click="confirmPrice">确定</button>
						</div>
					</div>
				</transition>
				<!-- 价格筛选 end -->
			</div>	
			<!-- 商品列表 -->
			<div class="product_lists">
				<ul
			  		v-infinite-scroll="loadMore"
			  		infinite-scroll-disabled="loading"
			  		infinite-scroll-distance="400">
					<li 
						class="d_flex" 
						@click="jumpDetail(lt,index)"
						v-for="(lt,index) in list">
						<div class="list_img">
							<img v-lazy="lt.defaultPhoto" :alt="lt.productName">
						</div>
						<div class="list_info">
							<h2><i v-show="lt.prescriptionType" :class="lt.prescriptionType | imgType"></i>{{ lt.productName }}</h2>
							<span>{{ lt.spec }}</span>
							<h3 class="r_color">{{ lt.ourPrice | priceFilter }}</h3>
							<!-- 活动暂不需要 -->
							<div v-if='false' class="activitys">
								<span class="inlie_block">套餐</span>
								<span class="inlie_block">秒杀</span>
								<span class="inlie_block">满减</span>
							</div>
						</div>
					</li>
				</ul>
				<div class="icon_loading">
					<span v-show="!showLoadMore">没有更多了</span>
					<mt-spinner  
						type="triple-bounce"
						v-show="showLoadMore"></mt-spinner>
				</div>
			</div>
			<!-- 商品列表 end -->
			<!-- 详细筛选 -->
			<div 
				class="detail_solt"
				v-show='curScreen===4'
				@click.self="closeLayer">
				<div class="top">
					<div class="item">
						<h3 class="cur"><span>是否进口</span></h3>
						<ul>
							<li 
								:class="{cur: isImported === 1}"
								@click="btnImport(1)">是</li>
							<li 
								:class="{cur: isImported === 0}"
								@click="btnImport(0)">否</li>
						</ul>
					</div>
					<div class="item">
						<h3 class="cur"><span>剂型</span></h3>
						<ul>
							<li 
								v-for="(jxitem,index) in jx"
								:class="{ cur: isJx === (index+1) }"
								@click="btnJx(jxitem,index)">{{ jxitem.jxName }}</li>
						</ul>
					</div>
					<!-- 底部按钮 -->
					<div class="btns d_flex">
						<button
							@click="resetSolt">重置</button>
						<button
							@click="confirmSolt">确定</button>
					</div>
					<!-- 底部按钮 end -->
				</div>
			</div>
			<!-- 详细筛选 end -->
			
			<!-- 加载动画 -->
			<div id="loadAnimate" v-show="showLoadAnimate">
				<div>
					<mt-spinner type="snake" color="#fff"></mt-spinner>
				</div>
			</div>
			<!-- 加载动画 end -->

			<!-- 遮罩 -->
			<transition name="fade">
			<div @click.self="closeLayer" class="layer" v-show="showLayer"></div>
			</transition>
			<!-- 遮罩 end -->	
		</div>
	</section>

	<!-- 子页面 -->
    <transition name="fade">
    	<router-view v-on:changeClass="changeClassOne"></router-view>
    </transition>
    <!-- 子页面 end -->
</section>
</template>

<script>
import { Field, Header, Button, TabContainer, TabContainerItem, Cell, Popup, Radio, Toast, Spinner } from 'mint-ui';

export default {
	name: 'Product',
	data (){
		return {
			flag: false,//
			selected: '1', //
			isUnscRollable: false, //是否页面不可滚动
			backParentPage: false, //是否回到父页面
			screensLists: [ //筛选列表
				{ name: '分类' },
				{ name: '品牌' },
				{ name: '价格' },
				{ name: '筛选' }
			], 
			showLayer: false, //是否显示遮罩
			// clickNum: '', //点击第几个筛选按钮
			clickNum: {
				1: { 'name': '分类', isCur: false, isData: false, },
				2: { 'name': '品牌', isCur: false, isData: false, },
				3: { 'name': '价格', isCur: false, isData: false, },
				4: { 'name': '筛选', isCur: false, isData: false, },
			},
			curScreen: '', // 当前展示的筛选
			list: [],
			page: 1, //页数 默认1
			searchCon: this.$route.query.searchCon,//搜索内容
			loading: false, //是否已在加载
			showLoadMore: false, //是否显示加载更多
			//筛选
			isImported: '',// 是否进口,0-否,1-是
			jx: [ //剂型
				{ id: 1, jxName: '粉剂' },
				{ id: 2, jxName: '贴剂' },
				{ id: 3, jxName: '口服散剂' },
				{ id: 4, jxName: '外用液体剂' },
				{ id: 5, jxName: '软膏剂' },
				{ id: 6, jxName: '栓剂' },
				{ id: 7, jxName: '口服液' },
				{ id: 8, jxName: '颗粒' },
				{ id: 9, jxName: '片剂' },
				{ id: 10, jxName: '胶囊' },
			],
			isJx: '', //选中的剂型
			jxName: '', //选中的剂型名称
			brandList: [], //品牌列表
			curBrandIndex: '',//当前选中的品牌
			curBrandId: '',//当前选中品牌ID
			minPrice: '',//最低价
			maxPrice: '',//最高价
			ks: [],//科室分类列表
			curKs: 0,//当前科室
			childSort: [], //二级分类
			parentName: '',//二级分类的父级
			curRightSort: '',//当前选中的分类
			productTypeID: this.$route.query.ptId?this.$route.query.ptId:'',//当前分类ID
			parentTypeId: this.$route.query.ptIdOne?this.$route.query.ptIdOne:'',//全部科室时传递的科室ID
			showLoadAnimate: false,//显示加载动画
		}
	},
	components: {
		'mt-field': Field,
		'mt-header': Header,
		'mt-button': Button,
	    'mt-tab-container': TabContainer,
	    'mt-tab-container-item': TabContainerItem,
    	'mt-cell': Cell,
    	'mt-popup': Popup,
    	'mt-radio': Radio,
    	'mt-spinner': Spinner,
	},
	filters: {
		// 图片类型 处方,OTC甲,OTC乙
		imgType ( data ){
			if( data == 1 ){
				return 'RX';
			}else if(data == 2){
				return 'OTC-2'
			}else if(data == 3){
				return 'OTC-1'
			}
		},
		//价格
		priceFilter ( data ){
			return '￥' + Number(data).toFixed(2);
		}
	},
	created (){
		const _self = this;
	},
	mounted (){
		this.$emit('changeClass',false);
	},
	destroyed (){
		this.$emit('changeClass',true);
	},
	methods: {
		//加载更多
		loadMore() {
		  const _self = this;

		  // alert('loadMore')
		  if(this.flag || this.isUnscRollable)return false;
		  this.loading = true;
		  this.showLoadMore = true;

		  _self.flag = true;	
		  setTimeout(() => {
		    // let last = this.list[this.list.length - 1];
			_self.$ajax({
				url: _self.webRoot + '/product/getProductList',
				// url: '/product/getProductList',
				data: {
					page: _self.page,
					limit: 20,
					productName: _self.searchCon, //产品名称
					isImport: _self.isImported, //是否进口
					productDrugType: _self.jxName, // 剂型
					brandId: _self.curBrandId, //品牌
					lowestPrice: _self.minPrice,//最低价格
					highest: _self.maxPrice,//最高价格
					productType_one: _self.parentTypeId,//父级ID
					productType_two: _self.productTypeID,//当前科室ID
					sysNo: 'wap', //终端
				}
			})
			.then(function(res){
				// console.log(res)
				if( res.data.success ){
					if( res.data.data.length == 0 ){
						// _self.$options.methods.showToast('没有更多数据!');
						_self.showLoadMore = false;
		    			_self.loading = true;
	    				_self.flag = false;
		    			return false;
					}
					// _self.list = res.data.data ;
					// _self.list.push( res.data.data );
					for( var i = 0;i<res.data.data.length;i++ ){
						_self.list.push( res.data.data[i] )
					}
	    			_self.loading = false;
					_self.showLoadMore = false;
		    		_self.page++;
		    		_self.flag = false;
				}else {
					_self.$options.methods.showToast(res.data.msg);
					_self.showLoadMore = false;
	    			_self.loading = true;
	    			_self.flag = false;
				}
			})
			.catch(function(err){
				console.log(err)
			});
		  	// this.showLoadMore = false;
		   //  this.loading = false;
		  }, 300);
		},
		//品牌选择
		chooseBrand ( bl,index ){
			this.curBrandIndex = index;
			this.curBrandId = bl.brandID;
		},
		//点击筛选
		clickScreens ( item, index ){
			const _self = this;
			//重复点击关闭筛选
			//第二次点击
			if( this.curScreen == index+1 ){
				// console.log('当前')
				if( !this.clickNum[index+1].isData ){ //没有点击筛选
					this.clickNum[index+1].isCur = false;
				}
				this.curScreen = '';//关闭筛选
				this.isUnscRollable = false;
				this.showLayer = false;//遮罩
				return false;
			}else{
				// console.log('非当前')
				if( !this.clickNum[index+1].isData ){ //没有点击筛选
					this.clickNum[index+1].isCur = false;
				}
				this.curScreen = '';//关闭筛选
				this.isUnscRollable = false;
				this.showLayer = false;//遮罩
				for(var i in this.clickNum) { 
					// console.log(i,":",this.clickNum[i])
					if( !this.clickNum[i].isData ){
						this.clickNum[i].isCur = false; 
					}	
				}
			}
			// end
			this.isUnscRollable = true;
			// console.log(this.clickNum)
			this.clickNum[index+1].isCur = true;
			if( index == 0 ){ //分类
				this.curScreen = 1;
				this.showLayer = true;
				if( this.ks.length>0 ){ //已有不重新加载
					return;
				}

				this.$ajax({
					url: _self.webRoot + '/productType/getAllOneLevelProductType',
					// url: '/productType/getAllOneLevelProductType',
				}).then(function(res){
					// console.log('1级科室分类 ', res)
					_self.ks = res.data;

					//默认获取第一个分类的内容
					_self.$ajax({
						url: _self.webRoot + '/productType/getAllTwoLevelProductType',
						// url: '/productType/getAllTwoLevelProductType',
						data: { parentTypeID: '' }
					}).then(function(res){
						// console.log('2级科室分类: ', res)
						var allSortData = [];
						for (var i = 0; i < res.data.length; i++) {
							if( res.data[i].parentTypeID != 0 ){
								allSortData.push(res.data[i]);
							}
						}
						_self.parentName = ''; //父级
						_self.childSort = allSortData;
					});

				});

			}else if( index == 1 ){ //品牌
				this.$ajax({
					url: _self.webRoot + '/brand/getBrandList',
					// url: '/brand/getBrandList',
				})
				.then(function(res){
					// console.log(res)
					if( res.data.success ){
						if(res.data.data.length == 0){
							_self.$options.methods.showToast('没有更多数据!');
							return false;
						}
						_self.brandList = res.data.data;
					}
				})
				.catch(function(err){
					console.log(err)
				})
				this.curScreen = 2;
				this.showLayer = true;
			}else if( index == 2 ){ //价格
				this.curScreen = 3;
				this.showLayer = true;
			}else if( index == 3 ){ //筛选
				this.curScreen = 4;
			}
		},
		//筛选是否进口
		btnImport ( data ){
			if( data == 1 ){
				this.isImported = 1;
			}else{
				this.isImported = 0;
			}
		},
		//选中的剂型
		btnJx ( jxitem, index ){
			this.isJx = jxitem.id;
		},
		//重置筛选
		resetSolt (){
			this.isImported = this.isJx = this.jxName = '';
			this.page = 1;
			this.$options.methods.getProduct( this );	
		},
		//确认筛选
		confirmSolt (){
			const _self = this;

			if( this.isImported === '' && this.isJx === '' ){
				return false;
			}

			for ( let i in this.jx ) {
				if( i == (this.isJx-1) ){
					_self.jxName = this.jx[this.isJx-1].jxName;
					break;
				}
			}

			this.page = 1;
			// this.$options.methods.getProduct( this );
			this.$options.methods.getData( this );
			//标志有筛选
			this.clickNum[4].isCur = this.clickNum[4].isData = true;
			// console.log(121231)
		},
		//重置品牌
		resetBrand (){
			this.curBrandIndex = this.curBrandId = '';
			this.page = 1;
			this.$options.methods.getProduct( this );
			this.$options.methods.ComCloseLayer( this );	
		},
		//确认品牌
		confirmBrand (){
			const _self = this;

			if( this.curBrandIndex === '' && this.curBrandId === '' ){
				return false;
			}
			this.$options.methods.getData( this );
			//标志有选品牌
			this.clickNum[2].isCur = this.clickNum[2].isData = true;
			// this.page = 1;
			// this.showLoadAnimate = true;
			// this.$options.methods.getProduct( this );
			// this.curScreen = '';
			// this.isUnscRollable = false;
			// this.showLayer = false;
		},
		//确认价格筛选
		confirmPrice (){
			var checkNum = /^\d+$/;
			if( !checkNum.test(this.minPrice) || !checkNum.test(this.maxPrice) ){
				this.$options.methods.showToast('请填写正确的价格区间!');
				return false;
			}
			if( this.minPrice >= this.maxPrice ){
				this.$options.methods.showToast('请填写正确的价格区间!');
				return false;
			}

			this.$options.methods.getData( this );
			//标志有选价格区间
			this.clickNum[3].isCur = this.clickNum[3].isData = true;
			// this.page = 1;
			// this.showLoadAnimate = true;
			// this.$options.methods.getProduct( this );
			// this.clickNum = '';
			// this.isUnscRollable = false;
			// this.curScreen = '';
			// this.showLayer = false;
		},
		// 点击查看下级分类
		nextSort ( item, index ){
			const _self = this;
			var parentTypeId = item.productTypeID;
			//清空已选的分类
			this.curRightSort = '',//当前选中的分类
			this.productTypeID = '',//当前分类ID

			this.curKs = index+1; //当前科室

			if( item == 'allSort' ){ 
				//全部分类
				this.curKs = 0;
				parentTypeId = '';

				//获取全部二级分类
				this.$ajax({
					url: _self.webRoot + '/productType/getAllTwoLevelProductType',
					// url: '/productType/getAllTwoLevelProductType',
					data: { parentTypeID: parentTypeId }
				}).then(function(res){
					// console.log('全部二级: ', res)
					var allSortData = [];
					for (var i = 0; i < res.data.length; i++) {
						if( res.data[i].parentTypeID != 0 ){
							allSortData.push(res.data[i]);
						}
					}
					_self.parentName = ''; //父级
					_self.childSort = allSortData;
				});
				return false;
			}

			this.$ajax({
				url: _self.webRoot + '/productType/getAllTwoLevelProductType',
				// url: '/productType/getAllTwoLevelProductType',
				data: { parentTypeID: parentTypeId }
			}).then(function(res){
				// console.log('获取二级: ', res)
				_self.parentName = item.productTypeName; //父级
				_self.childSort = res.data;
			});
			// console.log(this.curKs,index)
		},
		//点击2级分类搜索
		searchSort(cs,index) {
			// var parentTypeId = cs.parentTypeID;//父级ID
			var parentTypeId = '';//父级ID

			this.curRightSort = (index+1);
			this.productTypeID = cs.productTypeID;//选中的分类ID

			//全部二级分类
			if( cs == 'allSort' ){ 
				this.curRightSort = 0;
				//全部分类-全部
				if( this.curKs === 0 ){
					// console.log('全部分类-全部')
					this.curRightSort = 0;
					this.parentTypeId = this.productTypeID = '';//全部父级分类

					// console.log('allSort里： ', this.productTypeID, parentTypeId)
					this.$options.methods.getData( this );
					//标志有分类
					this.clickNum[1].isCur = this.clickNum[1].isData = false;
					return false;
				}else{
					// console.log('全部分类-全部xx科室分类')
					parentTypeId = this.ks[this.curKs-1].productTypeID;//当前科室的id
					// console.log('全部分类-对应的科室',parentTypeId)
					this.parentTypeId = parentTypeId;
				}
			}

			this.$options.methods.getData( this );
			//标志有分类
			this.clickNum[1].isCur = this.clickNum[1].isData = true;
		},
		//跳到搜索页
		jumpSearch (){
			// 选择的二级分类this.productTypeID， 选择的一级分类this.parentTypeId
			if( this.productTypeID  ){
				//二级
				this.$router.replace({ path: '/search', query:{ hotByIdTwo: this.productTypeID } });
			}else if( this.parentTypeId ){
				//一级
				this.$router.replace({ path: '/search', query:{ hotByIdOne: this.parentTypeId } });
			}
			else{
				this.$router.replace({ path: '/search' });
			}
			// this.$router.go(-1);
		},
		// 跳到商品详情页
		jumpDetail ( lt,index ){
			this.$router.push({ path: '/product_detail', query: {proid: lt.productID} });
		},
		//关闭遮罩
		closeLayer (){
			this.isUnscRollable = false;
			// this.clickNum = '';//清空筛选样式
			for(var i in this.clickNum) { 
				// console.log(i,":",this.clickNum[i])
				if( !this.clickNum[i].isData ){
					this.clickNum[i].isCur = false; 
				}	
			}

			this.curScreen = '';//关闭筛选
			this.showLayer = false;
		},
		ComCloseLayer ( _self ){
			_self.isUnscRollable = false;
			for(var i in _self.clickNum) { 
				if( !_self.clickNum[i].isData ){
					_self.clickNum[i].isCur = false; 
				}	
			}
			_self.curScreen = '';//关闭筛选
			_self.showLayer = false;
		},
		// 后退
		goBack (){	
			this.$router.go(-1);
		},
		//确定按钮
		btnSave (){	
			console.log('点击确定')
		},

		//公共函数
		//获取数据
		getData ( _self ){
			_self.page = 1; //重置页数
			_self.showLoadAnimate = true;//显示加载动画
			_self.$options.methods.getProduct( _self );//获取数据
			_self.curScreen = '';//关闭当前筛选
			_self.isUnscRollable = false;
			_self.showLayer = false;
		},
		//获取产品列表
		getProduct ( _self ){
			var parentTypeId = _self.parentTypeId;

			if(_self.flag)return false;
			if( !parentTypeId ){
				parentTypeId = '';
			}

			_self.flag = true;				
			_self.$ajax({
				url: _self.webRoot + '/product/getProductList',
				// url: '/product/getProductList',
				data: {
					page: _self.page,
					limit: 20,
					productName: _self.searchCon, //产品名称
					isImport: _self.isImported, //是否进口
					productDrugType: _self.jxName, // 剂型
					brandId: _self.curBrandId, //品牌
					lowestPrice: _self.minPrice,//最低价格
					highest: _self.maxPrice,//最高价格
					// productTypeId: _self.productTypeID,//分类id
					// parentTypeId: parentTypeId,
					productType_one: parentTypeId,
					productType_two: _self.productTypeID,
					sysNo: 'wap',//终端
				}
			})
			.then(function(res){
				// console.log('res: ',res)
				if( res.data.success ){
					if( res.data.data.length == 0 ){
						_self.$options.methods.showToast('没有更多数据!');
						// return false;
					}
					_self.list = [];
					setTimeout(function(){
						_self.list = res.data.data ;
					},1000);
				}else {
					_self.$options.methods.showToast(res.data.msg);
				}
				_self.flag = false;
				setTimeout(function(){
					_self.showLoadAnimate = false;
				},1000);
			})
			.catch(function(err){
				console.log(err)
			});
		},
	    //显示提示信息
	    showToast( msg, time ){
	      var time = time ? time : 2000;

	      Toast({
	        message: msg,
	        position: 'center',
	        duration: time
	      });     
	    },
	    // 判断是否返回父页面
	    changeClassOne ( isChildDestroyed ){
	      if( isChildDestroyed ){ 
	        //退出子页面
	        this.backParentPage = true; //返回首页
	        this.isUnscRollable = false;
	      }else{
	        //没退出子页面
	        this.backParentPage = false; // 没返回首页
	        this.isUnscRollable = true;
	      }
	    },
	},
}	
</script>

<style scoped>
@import './Css/product.css';
</style>


